
.cartDetailBackground {
  position: fixed;
  background: rgba(0, 0, 0, 0.7);
  top: 0;
  bottom: 0.92rem;
  left: 0;
  right: 0;
  z-index: 1;
  text-align: center;
  padding: 1.28rem 0.72rem 0 0.72rem;
}
.cartDetailBackground .cart-goods {
  position: fixed;
  bottom: 0.92rem;
  left: 0;
  right: 0;
  background: #ffffff;
}
.cartDetailBackground .cart-goods .cart {
  z-index: 9;
  height: 0.8rem;
  padding: 0 0.36rem;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: #f3f5f7;
  border-top: 0.03rem solid rgba(7, 17, 27, 0.1);
  border-bottom: 0.03rem solid rgba(7, 17, 27, 0.1);
}
.cartDetailBackground .cart-goods .cart > div:nth-child(1) {
  font-size: 0.28rem;
  font-weight: 200;
  color: #07111b;
  line-height: 0.8rem;
}
.cartDetailBackground .cart-goods .cart > div:nth-child(2) {
  font-size: 0.24rem;
  color: #00a0dc;
  line-height: 0.8rem;
}
.cartDetailBackground .cart-goods .cartgoods {
  max-height: 4rem;
  overflow: hidden;
}
.cartDetailBackground .cart-goods .cartgoods > div:nth-child(1) {
  padding: 0rem 0.36rem;
}
.cartDetailBackground .cart-goods .cartgoods > div:nth-child(1) .cartgood {
  border-bottom: 0.02rem solid rgba(7, 17, 27, 0.1);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.cartDetailBackground .cart-goods .cartgoods > div:nth-child(1) .cartgood .name {
  font-size: 0.28rem;
  line-height: 0.48rem;
  color: #07111b;
}
.cartDetailBackground .cart-goods .cartgoods > div:nth-child(1) .cartgood .price-plus-or-minus {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.cartDetailBackground .cart-goods .cartgoods > div:nth-child(1) .cartgood .price-plus-or-minus .totalPrice {
  padding: 0rem 0.24rem 0 0.36rem;
}
.cartDetailBackground .cart-goods .cartgoods > div:nth-child(1) .cartgood .price-plus-or-minus .totalPrice > span:nth-child(1),
.cartDetailBackground .cart-goods .cartgoods > div:nth-child(1) .cartgood .price-plus-or-minus .totalPrice > span:nth-child(2) {
  color: #f01414;
  line-height: 0.8rem;
}
.cartDetailBackground .cart-goods .cartgoods > div:nth-child(1) .cartgood .price-plus-or-minus .totalPrice > span:nth-child(1) {
  font-size: 0.2rem;
  font-weight: normal;
}
.cartDetailBackground .cart-goods .cartgoods > div:nth-child(1) .cartgood .price-plus-or-minus .totalPrice > span:nth-child(2) {
  font-size: 0.28rem;
  font-weight: 700;
}



