
.detailBackground {
  position: fixed;
  background: rgba(0, 0, 0, 0.7);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  text-align: center;
  padding: 1.28rem 0.72rem 0 0.72rem;
}
.detailBackground .sticky_footer {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.detailBackground .sticky_footer .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  /*
				星星
				*/
  /*
				分割线
				*/
  /*
				商家公告
				*/
}
.detailBackground .sticky_footer .content .seller_title {
  font-size: 0.32rem;
}
.detailBackground .sticky_footer .content .stars {
  margin: 0.32rem 1.12rem 0.56rem;
}
.detailBackground .sticky_footer .content .stars img {
  height: 0.48rem;
}
.detailBackground .sticky_footer .content .split {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.detailBackground .sticky_footer .content .split .line {
  background-color: rgba(255, 255, 255, 0.2);
  width: 2.24rem;
  height: 0.04rem;
}
.detailBackground .sticky_footer .content .split .info {
  font-size: 0.28rem;
  font-weight: 700;
  color: #ffffff;
}
.detailBackground .sticky_footer .content #icons {
  padding: 0.24rem 0.24rem 0.56rem ;
}
.detailBackground .sticky_footer .content #icons .decrease,
.detailBackground .sticky_footer .content #icons .discount,
.detailBackground .sticky_footer .content #icons .guarantee,
.detailBackground .sticky_footer .content #icons .invoice,
.detailBackground .sticky_footer .content #icons .special {
  width: 0.32rem;
  height: 0.32rem;
  margin: 0.24rem 0.12rem 0 0;
}
.detailBackground .sticky_footer .content #icons .description {
  margin: 0.24rem 0 0;
  font-size: 0.24rem;
  line-height: 0.24rem;
  font-weight: 200;
  color: #ffffff;
}
.detailBackground .sticky_footer .content .seller_notice_content {
  text-align: left;
  font-size: 0.24rem;
  color: #ffffff;
  line-height: 0.48rem;
  padding: 0.48rem 0.24rem 0;
}
.detailBackground .close {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  margin-bottom: 0.64rem;
}
.detailBackground .close .icon-X {
  font-size: 0.64rem;
  color: rgba(255, 255, 255, 0.5);
}



