  top: 3.48rem;
  bottom: 0.92rem;
  width: 100%;
  overflow: hidden;
}
.rantings .header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0.36rem 0.48rem;
}
.rantings .header .header_left {
